        header#header #navbar nav#nav ul li::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 0;
          height: 100%;
          z-index: -1;
          background-color: #d90217;
          transition: width 0.5s ease-in-out;
          -webkit-transition: width 0.5s ease-in-out;
