.skewYTop {
  position: relative;
  padding: 300px 0px 100px 0px;
  z-index: 2;
  background-color: rgba(217, 2, 23, 0.8); }
  .skewYTop .skew-bg, .skewYTop::after {
    position: absolute;
    width: 100%;
