    left: 0;
    z-index: -1; }
  .skewYTop .skew-bg {
    background-size: cover;
    background-position: center -250px;
    height: 100%;
    top: 0; }
    .skewYTop .skew-bg::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(255, 0, 25, 0.7); }
  .skewYTop::after {
    content: '';
    height: 250px;
    top: -125px;
    background-color: #ffffff;
    -moz-transform: skewY(-7deg);
    transform: skewY(-7deg);
    -webkit-transform: skewY(-7deg);
    -ms-transform: skewY(-7deg);
    -ms-filter: "progid:DXImageTransform.Microsoft.Matrix(M11=1, M12=0, M21=-0.12278456090290474, M22=1, SizingMethod=" auto expand ")";
    filter: progid:DXImageTransform.Microsoft.Matrix(M11=1,M12=0,M21=-0.12278456090290474,M22=1,SizingMethod='auto expand'); }

.skewY {
